@charset "UTF-8";
:root {
  --color_1: hsl(0, 21%, 94%);
  --color_2: hsl(0, 4%, 9%);
  --color_3: hsl(0, 87%, 54%);
  --color_4: hsl(0, 3%, 13%);
  --color_5: hsl(157, 4%, 36%);
  --header-bg:var(--color_1);
  --typography: var(--color_2);
  --link-header: var(--color_5);
  --hero-bg:var(--color_2);
  --hero-info:var(--color_4);
  --hover:var(--color_3);
  /* Шрифты для текста */
  --title-bi: 3.5rem;
  --title-l: 2.5rem;
  --title-m: 1.75rem;
  --title-s: 1.25rem;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  list-style: none;
}

button, input {
  font-family: inherit;
  border: none;
}

body {
  font-family: sans-serif;
  min-height: 100vh;
}

#rain {
  display: block;
  /* убирает встроенный inline-gap */
  width: 100%;
  max-width: 100%;
  background-color: transparent;
}

.layers {
  perspective: 1000px;
  overflow: hidden;
}
.layers__container {
  position: relative;
  height: 100vh;
  transform-style: preserve-3d;
  transform: rotateX(var(--move-y)) rotateY(var(--move-x));
}
.layers__item {
  position: absolute;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.layers__item--1 {
  inset: -7vw;
  filter: brightness(1.2);
  transform: translateZ(-55px) scale(1.06);
}
.layers__item--2 {
  inset: -7vw;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateZ(80px) scale(0.8);
}
.layers__item--3 {
  width: 100%;
  height: 100%;
  right: -20%;
  pointer-events: none;
  transform: translateZ(8px) scale(0.8);
}
.layers__item--4 {
  top: 10%;
  right: 20%;
  width: 230px;
  height: 200px;
  transform: translateZ(190px) scale(0.8);
  pointer-events: none;
}
.layers__item--5 {
  top: 50%;
  left: 20%;
  width: 300px;
  height: 300px;
  transform: translateZ(300px) scale(0.9);
  pointer-events: none;
}
.layers__item--6 {
  top: 0;
  left: -50%;
  width: 100%;
  height: 100%;
  transform: translateZ(380px);
  pointer-events: none;
}

.hero-content {
  text-align: center;
  color: var(--color_1);
  text-shadow: 0 0 5px black;
}
.hero-content__title {
  font-size: 70px;
  margin-bottom: 10px;
  font-family: "PT Serif", serif;
}
.hero-content__text {
  margin-bottom: 20px;
  font-size: 30px;
  font-family: "PT Sans", sans-serif;
}

.hero-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 1em 2em;
  border: none;
  outline: none;
  color: var(--color_1);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}
.hero-button:before {
  content: "";
  background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}
.hero-button::after {
  background: transparent;
}
.hero-button:active {
  color: #ffffff;
}
.hero-button:active:after {
  background: transparent;
}
.hero-button:hover:before {
  opacity: 1;
}
.hero-button:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}